<template>
  <div>
    <input
      type="file"
      :ref="refName"
      style="display: none"
      @change="fileChange"
    />
  </div>
</template>

<script>
import API from '@/api/application'
import { authKey } from '@/router'
import notification from '@/components/notification'
export default {
  components: {},
  props: {},
  data() {
    return {
      refName: 'inputFile',
    }
  },
  created() {},
  mounted() {},
  methods: {
    open() {
      this.$refs[this.refName].click()
      // this.successTips({ data: { appId: 1, importSuccess: true, renamed: true } })
    },
    fileChange() {
      const [file] = this.$refs[this.refName].files
      this.recieveFile(file)
    },
    // 接收文件上传,可从外部传入文件
    recieveFile(file) {
      const { $refs, refName } = this
      const input = $refs[refName]
      if (!file) {
        return console.log('not has any file')
      }
      if (!this.judgeFile(file)) {
        return
      }
      const formData = new FormData()
      formData.append('file', file)
      this.$http({
        url: API.appInfoImport,
        headers: {
          'Content-Type': 'multipart/form-data',
        },
        customData: formData,
      })
        .then(async (res) => {
          this.$emit('success')
          this.successTips(res)
        })
        .finally(() => {
          input.value = ''
        })
    },
    judgeFile(file) {
      const types = ['zip']
      const include = types.some((type) => file.name.endsWith(type))
      if (!include) {
        this.$message.error('文件格式不合法')
        return false
      }
      return true
    },
    successTips(res) {
      let icon
      let message
      let btnStr
      const { appId, importSuccess, renamed } = res.data
      if (importSuccess) {
        if (renamed) {
          icon = 'warning'
          message = '导入成功，已修改重复应用名'
        } else {
          icon = 'success'
          message = '导入成功'
        }
        btnStr = '进入应用'
      } else {
        btnStr = '选择文件'
        icon = 'error'
        message = '导入失败, 请重新选择文件'
      }

      const h = this.$createElement
      const button = h(
        'span',
        {
          class: 'message-button',
          on: {
            click: () => {
              if (importSuccess) {
                rootApp.$router.push({
                  name: authKey.AppDetail,
                  query: {
                    id: appId,
                  },
                })
              } else {
                this.open()
              }
            },
          },
        },
        btnStr
      )
      notification({
        position: 'top-center',
        customClass: 'download-notifiy',
        type: icon,
        dangerouslyUseHTMLString: true,
        showClose: false,
        message: h('div', { class: 'message-content' }, [
          h(
            'span',
            {
              class: 'message-str',
            },
            message
          ),
          button,
        ]),
      })
    },
  },

  computed: {},

  watch: {},
}
</script>
<style lang='less'>
.download-notifiy {
  display: flex;
  .el-notification__group {
    flex: 1;
  }
  .el-notification__content {
    margin: 0;
  }
  .message-content {
    display: flex;
    justify-content: space-between;
    line-height: 25px;
    .message-str {
      font-family: PingFangSC-Regular;
      font-size: 12px;
      color: #666666;
      letter-spacing: 0;
      font-weight: 400;
    }
    .message-button {
      font-family: PingFangSC-Regular;
      font-size: 12px;
      color: #3f92fe;
      letter-spacing: 0;
      font-weight: 400;
      cursor: pointer;
    }
  }
}
</style>