              @keyup.enter.native="getAppList(true)"
              placeholder="输入分类或应用名称搜索"
            >
              <el-button
                slot="append"
                icon="el-icon-search"
                @click="getAppList(true)"
              ></el-button>
            </el-input>
          </n-form-item>
        </div>
        <div class="tools-bar">
