          </div>
        </div>
      </n-form>
    </header>
    <main class="main" v-loading="isAppListLoading">
      <div class="app-list">
        <!-- <div class="app-item item-add">
          <div class="normal" @click="addApp">
            <i class="iconfont icon-biaoqian-zengjia item-add-icon"></i>
            <div class="item-add-btn">添加应用</div>
