            <el-button class="create-button">导入应用</el-button>
          </div>
        </div> -->
        <div class="app-item" v-for="app in appList" :key="app.id">
          <div class="app-item-inner" v-ripple @click.stop="handleSelectApp(app)">
            <div class="app-item-left">
              <div class="app-icon">
                <img :src="getImageUrl(app.icon, true, app.id) || defaultIcon" />
              </div>
            </div>
            <div class="app-item-right">
              <div class="app-name">
                {{ app.name }}
              </div>
              <!-- <el-rate class="app-rate" :value="3.8" disabled>
                              </el-rate> -->
              <div class="app-tag">{{ app.category }}</div>
              <div class="app-version">
                <div class="app-version-item" :title="app.publishName">
                  最新名称：{{ app.publishName }}
                </div>
                <div class="app-version-item" :title="app.latestVersion">
                  最新版本：{{ app.latestVersion }}
                </div>
                <div class="app-version-item" :title="app.publishVersion">
                  上架版本：{{ app.publishVersion || '-' }}
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </main>
    <footer class="footer">
      <pagination
        :page="searchForm.page"
        :limit="searchForm.pageSize"
        :total="total"
        :page-sizes="pageSizes"
        @pagination="handlePageChange"
      />
    </footer>
    <create-app :id="0" ref="createApp" @success="getAppList"></create-app>
    <import-app ref="importApp" @success="getAppList"></import-app>
