<script>
//func
import { getImageUrl, getImgPrefix } from '@/utils'
//url
import API from '@/api/application.js'

//static
const defaultIcon = getImgPrefix('/application/app.png')

//components
import Pagination from '@/components/Pagination/index.vue'
import Select from '@/components/Select'
import AppClassify from './components/appClassify'
import CreateApp from './components/createApp'
import ImportApp from './components/importApp'
import { authKey } from '@/router'
export default {
  name: 'Application',
  components: {
    Pagination,
    Select,
    AppClassify,
    CreateApp,
    ImportApp,
  },
  data() {
    return {
      getImageUrl,
      defaultIcon,
      searchForm: {
        keyword: '',
        page: 1,
        pageSize: 12,
        sort: '',
        categoryCode: '',
      },
      total: 0,
      pageSizes: [12, 36, 72, 200],
      orderTypes: [
        {
          label: '按创建时间升序',
          value: 0,
          keyName: 'created_time',
          sortName: 'asc',
        },
        {
          label: '按创建时间倒序',
          value: 1,
          keyName: 'created_time',
          sortName: 'desc',
        },
        {
          label: '按应用名称升序',
          value: 2,
          keyName: 'name',
          sortName: 'asc',
        },
        {
          label: '按应用名称倒序',
          value: 3,
          keyName: 'name',
          sortName: 'desc',
        },
      ],
      classiessList: [],
      isAppListLoading: false,
      appList: [],
    }
  },
  methods: {
    handlePageChange({ page, size }) {
      this.searchForm.page = page
      this.searchForm.pageSize = size
      this.getAppList()
    },
    handleSelectApp(app) {
      rootApp.$router.push({
        name: authKey.AppDetail,
        query: {
          id: app.id,
        },
      })
    },

    getParams(pageReset = false) {
      const { page, pageSize, categoryCode, keyword, sort } = this.searchForm
      const { keyName, sortName } =
        this.orderTypes.find((item) => item.value === sort) || {}
      return {
        condition: {
          categoryCode,
          keyword,
        },
        pagination: {
          pagenum: pageReset === true ? 1 : page,
          pagesize: pageSize,
          sort: {
            name: keyName,
            type: sortName,
          },
          sorting: '',
        },
      }
    },
    // 获取应用列表
    getAppList(pageReset = false) {
      this.isAppListLoading = true
      this.$http({
        url: API.appInfoPage,
        data: this.getParams(pageReset),
      })
        .then((res) => {
          const { records, rowCount } = res.data
          this.appList = records
          this.total = rowCount
        })
        .finally(() => {
          this.isAppListLoading = false
        })
    },

    addApp() {
      this.$refs.createApp.show()
    },
    importApp() {
      this.$refs.importApp.open()
    },
    handleCommand(command) {
      const action = {
        create: this.addApp,
        import: this.importApp,
        default: () => {
          console.log('default')
        },
      }[command || 'default']
      action()
    },
  },
  watch: {},
  created() {
    this.getAppList()
  },
}
</script>

